import React, { useEffect } from 'react';
import { Row, Col, Form } from 'antd';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';
import { requiredRule } from 'utils/validateRules';
import { getTrainTypes } from 'services/trainType';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';

const SearchBar = props => {
  useEffect(() => {
    const { form } = props;
    const queryParams = getQueryParams();
    delete queryParams._time;
    form.setFieldsValue(queryParams);
  }, []);

  const handleSearch = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  const handleReset = e => {
    props.form.resetFields();
    handleSearch(e);
  };

  const { getFieldDecorator, setFieldsValue } = props.form;
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };

  return (
    <Form className="form-inline-label" layout="vertical" onSubmit={handleSearch}>
      <Row gutter={24}>
        <Col span={6}>
          <Form.Item {...formItemLayout} label="H" required>
            {getFieldDecorator('trainTypeId', { rules: [requiredRule] })(
              <SelectAjax service={getTrainTypes} />
            )}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item {...formItemLayout} label="Nm hc">
            {getFieldDecorator(
              'yearEduId',
              {}
            )(
              <SelectAjax
                service={getYearEducations}
                setFieldsValue={setFieldsValue}
                typeValue="int"
              />
            )}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item {...formItemLayout} label="Hc k">
            {getFieldDecorator(
              'semesterId',
              {}
            )(
              <SelectAjax service={getSemesters} setFieldsValue={setFieldsValue} typeValue="int" />
            )}
          </Form.Item>
        </Col>
        <Col span={6}>
          <div className="btn-group">
            <BtnSearch htmlType="submit" title="Tm kim" />
            <BtnClear onClick={handleReset} />
          </div>
        </Col>
      </Row>
    </Form>
  );
};

export default Form.create()(SearchBar);



