import React, { useState, useEffect } from 'react';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { dateToString } from 'utils/formatUtil';
import { getQueryParams } from 'utils/urlUtil';
import { getRegisterSession, deleteRegisterSession } from 'services/registerSession';
import SearchBar from './SearchBar';

const RegisterSession = () => {
  const [reload, setReload] = useState(0);
  const [paramSearch, setParamSearch] = useState({});

  useEffect(() => {
    const queryParams = getQueryParams();
    if (
      queryParams.trainTypeId !== paramSearch.trainTypeId ||
      queryParams.yearEduId !== paramSearch.yearEduId ||
      queryParams.semesterId !== paramSearch.semesterId
    ) {
      setParamSearch(getQueryParams());
    }
  });

  const getRegisterSessionAddIndex = async paramSearchReq => {
    const res = await getRegisterSession(paramSearchReq);

    const newItems = res.data.items.map((value, index) => {
      const newValue = value;
      newValue.index = index + 1;
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  const handleDelete = async id => {
    try {
      await deleteRegisterSession(id);
      message.success('Xa thnh cng');
      setReload(reload + 1);
    } catch (err) {
      console.log(err);
    }
  };

  const columns = [
    {
      title: 'STT',
      dataIndex: 'index',
      key: 'index',
      width: '100px',
    },
    {
      title: 'H',
      dataIndex: 'trainTypeLabel',
      key: 'trainTypeLabel',
    },
    {
      title: 'Nm hc',
      dataIndex: 'yearEduLabel',
      key: 'yearEduLabel',
    },
    {
      title: 'Hc k',
      dataIndex: 'semesterLabel',
      key: 'semesterLabel',
    },
    {
      title: 't ng k',
      dataIndex: 'sessionNo',
      key: 'sessionNo',
    },
    {
      title: 'Ngy bt u',
      dataIndex: 'startDate',
      key: 'startDate',
      render: text => dateToString(text),
    },
    {
      title: 'Ngy kt thc',
      dataIndex: 'endDate',
      key: 'endDate',
      render: text => dateToString(text),
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          linkEdit={{
            to: {
              pathname: routeConstant.THOI_KHOA_BIEU_TIN_CHI__DOT_DANG_KY__NHAP_DU_LIEU,
              search: `?id=${record.id}`,
            },
          }}
          onConfirmDelete={() => handleDelete(record.id)}
        />
      ),
    },
  ];

  return (
    <Card title="t ng k">
      <CartHeadAction>
        <Link to={routeConstant.THOI_KHOA_BIEU_TIN_CHI__DOT_DANG_KY__NHAP_DU_LIEU}>
          <BtnAddNew />
        </Link>
      </CartHeadAction>
      <SearchBar />
      <Row>
        <Col span={24}>
          <TableData
            reload={reload}
            columns={columns}
            service={getRegisterSessionAddIndex}
            paramSearch={paramSearch}
            allowGetData={!!paramSearch.trainTypeId}
            rowKey="id"
          />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <RegisterSession />
  </Page>
);



